.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}


.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
    
}

.posters::-webkit-scrollbar{
    display: none;
}

.smallPoster{
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;
   
}
.poster:hover{
    transform: scale(1.1);
    transition: transform 0.3s ease;
}

.smallPoster:hover{
    transform: scale(1.1);
    transition: transform 0.3s ease;
}
